/* Media queries */
html,
body {
  height: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: center;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

body {
  font-size: 1.6rem;
  font-family: "Montserrat", sans-serif;
  background: linear-gradient(64.5deg, rgb(245, 116, 185) 14.7%, rgb(89, 97, 223) 88.7%);
}

*,
*::before,
::after {
  box-sizing: inherit;
}

body,
h1,
h2,
h3,
p {
  margin: 0;
}

.main-container {
  margin-block-start: 5rem;
  width: 500px;
  border-radius: 20px;
  box-shadow: 8px 8px 25px 1px rgba(0, 0, 0, 0.4);
  background-color: #323249;
}

.header-container {
  width: 100%;
  height: 20%;
  background: linear-gradient(to left, rgb(89, 97, 223), #797ebe);
  border-radius: 20px;
  display: flex;
  padding: 2.5rem;
  position: relative;
}

.date-info {
  width: 80%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.date-info .day,
.date-info .number-day,
.date-info .month {
  color: #fff;
}
.date-info .day {
  font-size: 3rem;
  font-weight: 600;
}
.date-info .number-day {
  font-size: 2.5rem;
  font-weight: 400;
}
.date-info .month {
  font-size: 1.8rem;
  font-weight: 400;
}

.tasks-info {
  width: 20%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: 1.5rem;
  font-weight: 500;
}

.addCancel-task-button {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #e93595;
  border: none;
  border-radius: 50%;
  box-shadow: 8px 8px 25px 1px rgba(0, 0, 0, 0.4);
  position: absolute;
  inset-block-end: -1.8rem;
  inset-inline-end: 1.6rem;
}
.addCancel-task-button:hover {
  cursor: pointer;
  background-color: #d4177c;
}

.addCancel-task-icon {
  width: 25px;
  height: 25px;
  fill: #fff;
}

.show-icon {
  display: block;
}

.hidden-icon {
  display: none;
}

.add-task-container {
  width: 100%;
  display: none;
  padding: 2rem;
  background-color: #323249;
}

.showAdd-task-container {
  display: block;
}

.task-form {
  height: max-content;
  display: flex;
  flex-direction: column;
  row-gap: 1.2rem;
}

.form-group {
  display: flex;
  flex-direction: column;
}
.form-group label {
  font-size: 1.2rem;
  font-weight: 500;
  color: #fff;
}
.form-group input[type=text] {
  height: 4rem;
  font-size: 1.6rem;
  color: #fff;
  border: none;
  background-color: transparent;
}
.form-group input[type=text]:focus {
  outline: none;
}

input[type=submit] {
  align-self: flex-end;
  padding: 0.8rem 2rem;
  font-size: 1.5rem;
  font-weight: 600;
  color: #fff;
  border: none;
  border-radius: 6px;
  background-color: #e93595;
}
input[type=submit]:hover {
  cursor: pointer;
  background-color: #d4177c;
}

.alert-message-container {
  width: 100%;
  margin-block-end: 2rem;
  padding: 1rem;
  border-radius: 6px;
}

.alert-message {
  font-size: 1.3rem;
  color: #fff;
  text-align: center;
}

.alert-message-error {
  background-color: #e93595;
}

.alert-message-success {
  background-color: green;
}

.tasks-container {
  width: inherit;
  height: 80%;
  background-color: #323249;
  border-radius: 0 0 20px 20px;
}